          <view v-if="errors.idCard" class="error-tip">{{ errors.idCard }}</view>
        </view>
        <view class="form-group">
          <textarea v-model="form.address" placeholder="ËØ∑ËæìÂÖ•ËÅîÁ≥ªÂú∞ÂùÄ" class="textarea" maxlength="50" />
          <view v-if="errors.address" class="error-tip">{{ errors.address }}</view>
        </view>
      </view>

      <!-- Á•àÁ¶èËØ≠ -->
      <view class="card">
        <view class="card-title">
          <text class="icon">üôè</text>Á•àÁ¶èËØ≠ÔºàÂèØÈÄâÔºâ
