            class="textarea" 
            maxlength="100" 
            @input="onWishInput" 
          />
          <view class="word-count">{{ form.wish.length }}/100</view>
        </view>
      </view>

      <!-- Êî∂‰ª∂‰ø°ÊÅØ -->
