        <view class="card-title">
          <text class="icon">üì¶</text>Êî∂‰ª∂‰ø°ÊÅØ
        </view>
        <view class="form-group">
          <input v-model="form.receiver.name" placeholder="Êî∂‰ª∂‰∫∫ÂßìÂêç" class="input" maxlength="10" />
        </view>
        <view class="form-group">
          <input v-model="form.receiver.phone" placeholder="Êî∂‰ª∂‰∫∫ÁîµËØù" class="input" maxlength="11" />
        </view>
        <view class="form-group">
          <textarea v-model="form.receiver.address" placeholder="ËØ¶ÁªÜÊî∂‰ª∂Âú∞ÂùÄ" class="textarea" maxlength="100" />
        </view>
        <view class="form-group">
          <input v-model="form.receiver.remark" placeholder="Â§áÊ≥®‰ø°ÊÅØÔºàÂèØÈÄâÔºâ" class="input" maxlength="50" />
        </view>
      </view>

      <view class="btn-group">
        <button class="form-btn" type="primary" :loading="loading" @click="nextStep">
          <text v-if="!loading">‰∏ã‰∏ÄÊ≠•</text>
          <text v-else>Êèê‰∫§‰∏≠...</text>
        </button>
        <button class="form-btn reset" type="default" @click="resetForm">ÈáçÁΩÆ</button>
      </view>
      
      <!-- ËøîÂõûÊåâÈíÆ -->
      <view class="back-section">
        <button class="back-btn" @click="goBack">ËøîÂõû‰∏äÈ°µ</button>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      hallIndex: -1,
      showReceiver: true,
      loading: false,
      halls: ['Â§ßÈõÑÂÆùÊÆø', 'ËßÇÈü≥ÊÆø', 'Âú∞ËóèÊÆø', 'ËçØÂ∏àÊÆø', 'ÊñáÊÆäÊÆø'],
      offerings: [
        {
          icon: 'üïØÔ∏è',
          name: 'È¶ô',
          description: 'Ê∏ÖÈ¶ô‰∏ÄÁÇ∑ÔºåÁ•àÊÑøÂπ≥ÂÆâ',
          price: 20,
          unit: 'ÁÇ∑',
          qty: 0
        },
        {
          icon: 'üå∏',
          name: 'Ëä±',
          description: 'È≤úËä±‰æõÂìÅÔºåÁ•àÊÑøÂêâÁ••',
          price: 30,
          unit: 'Êùü',
          qty: 0
        },
        {
          icon: 'üí°',
          name: 'ÁÅØ',
          description: 'ÊòéÁÅØ‰∏ÄÁõèÔºåÁ•àÊÑøÂÖâÊòé',
          price: 50,
          unit: 'Áõè',
          qty: 0
        },
        {
          icon: 'üíß',
          name: 'Ê∞¥',
          description: 'ÂáÄÊ∞¥‰∏ÄÊùØÔºåÁ•àÊÑøÊ∏ÖÂáÄ',
          price: 10,
          unit: 'ÊùØ',
          qty: 0
        },
        {
          icon: 'üçé',
          name: 'Êûú',
          description: 'È≤úÊûú‰æõÂìÅÔºåÁ•àÊÑøÂúÜÊª°',
          price: 40,
          unit: '‰ªΩ',
          qty: 0
        },
        {
          icon: 'üçö',
          name: 'È£ü',
          description: 'Á¥†È£ü‰æõÂìÅÔºåÁ•àÊÑøÂÅ•Â∫∑',
          price: 25,
          unit: '‰ªΩ',
          qty: 0
        }
      ],
      form: {
        name: '',
        phone: '',
        idCard: '',
        address: '',
        wish: '',
        receiver: {
          name: '',
          phone: '',
          address: '',
          remark: ''
        }
      },
      errors: {},
      shippingFee: 15
    }
  },
  computed: {
    totalPrice() {
      return this.offerings.reduce((sum, item) => sum + item.price * item.qty, 0)
    },
    selectedOfferings() {
      return this.offerings.filter(item => item.qty > 0)
    }
  },
  methods: {
    onSelectHall(e) {
      this.hallIndex = e.detail.value
      this.errors = {}
    },
    increaseQty(index) {
      this.offerings[index].qty++
      this.errors = {}
    },
    decreaseQty(index) {
      if (this.offerings[index].qty > 0) {
        this.offerings[index].qty--
      }
      this.errors = {}
    },
    onWishInput() {
      if (this.form.wish.length > 100) {
        this.form.wish = this.form.wish.slice(0, 100)
      }
    },
    validate() {
      const errors = {}
      
      if (this.hallIndex < 0) errors.hallIndex = 'ËØ∑ÈÄâÊã©ÊÆøÂ†Ç'
      
      // Ê£ÄÊü•ÊòØÂê¶ÈÄâÊã©‰∫Ü‰æõÂìÅ
      const hasOfferings = this.offerings.some(item => item.qty > 0)
      if (!hasOfferings) {
        errors.offerings = 'ËØ∑Ëá≥Â∞ëÈÄâÊã©‰∏ÄÁßç‰æõÂìÅ'
      }
      
      if (!this.form.name) errors.name = 'ËØ∑ËæìÂÖ•ÂßìÂêç'
      else if (this.form.name.length < 2) errors.name = 'ÂßìÂêçÈúÄ2-10Â≠ó'
      
      if (!this.form.phone) errors.phone = 'ËØ∑ËæìÂÖ•ËÅîÁ≥ªÁîµËØù'
      else if (!/^1[3-9]\d{9}$/.test(this.form.phone)) errors.phone = 'ÊâãÊú∫Âè∑Ê†ºÂºè‰∏çÊ≠£Á°Æ'
      
      if (!this.form.idCard) errors.idCard = 'ËØ∑ËæìÂÖ•Ë∫´‰ªΩËØÅÂè∑'
      else if (!/^\d{17}[\dXx]$/.test(this.form.idCard)) errors.idCard = 'Ë∫´‰ªΩËØÅÂè∑Ê†ºÂºè‰∏çÊ≠£Á°Æ'
      
      if (!this.form.address) errors.address = 'ËØ∑ËæìÂÖ•ËÅîÁ≥ªÂú∞ÂùÄ'
      
      this.errors = errors
      return Object.keys(errors).length === 0
    },
    nextStep() {
      if (!this.validate()) {
        uni.showToast({ title: 'ËØ∑ÂÆåÂñÑË°®Âçï‰ø°ÊÅØ', icon: 'none' })
        return
      }
      
      this.loading = true
      
      // ÊûÑÂª∫Á°ÆËÆ§Êï∞ÊçÆ
      const confirmData = {
        hallId: this.hallIndex,
        hallName: this.halls[this.hallIndex],
        offerings: this.selectedOfferings,
        totalPrice: this.totalPrice,
        name: this.form.name,
        phone: this.form.phone,
        idCard: this.form.idCard,
        address: this.form.address,
        wish: this.form.wish,
        receiver: this.form.receiver,
        timestamp: new Date().getTime()
      }
      
      // ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
      uni.setStorageSync('hallOrder', confirmData)
      
      setTimeout(() => {
        this.loading = false
        // Ë∑≥ËΩ¨Âà∞Á°ÆËÆ§È°µ
        uni.navigateTo({ 
          url: '/pages/hall/confirm',
          success: () => {
            console.log('Ë∑≥ËΩ¨Âà∞ÊÆøÂ†Ç‰æõÂìÅÁ°ÆËÆ§È°µÊàêÂäü')
          },
          fail: (err) => {
            console.error('Ë∑≥ËΩ¨Â§±Ë¥•:', err)
            uni.showToast({ title: 'È°µÈù¢Ë∑≥ËΩ¨Â§±Ë¥•', icon: 'none' })
          }
        })
      }, 800)
    },
    resetForm() {
      this.hallIndex = -1
      this.form = {
        name: '',
        phone: '',
        idCard: '',
        address: '',
        wish: '',
        receiver: {
          name: '',
          phone: '',
          address: '',
          remark: ''
        }
      }
      this.offerings.forEach(item => item.qty = 0)
      this.errors = {}
    },
    goBack() {
      uni.navigateBack({
        success: () => {
          console.log('ËøîÂõû‰∏äÈ°µÊàêÂäü')
        },
        fail: (err) => {
          console.error('ËøîÂõû‰∏äÈ°µÂ§±Ë¥•:', err)
          // Â¶ÇÊûúËøîÂõûÂ§±Ë¥•ÔºåË∑≥ËΩ¨Âà∞ÊÆøÂ†Ç‰æõÂìÅ‰∏ªÈ°µ
          uni.navigateTo({ 
            url: '/pages/hall/index',
            fail: () => {
              uni.showToast({ 
                title: 'ËøîÂõûÂ§±Ë¥•ÔºåËØ∑ÈáçËØï', 
                icon: 'none',
                duration: 2000
              })
            }
          })
        }
      })
    }
  },
  onLoad() {
    // Ëá™Âä®Â°´ÂÖÖ‰∏äÊ¨°Â°´ÂÜô
    const last = uni.getStorageSync('hallOrder')
    if (last) {
      this.hallIndex = last.hallId || -1
      this.form.name = last.name || ''
      this.form.phone = last.phone || ''
      this.form.idCard = last.idCard || ''
      this.form.address = last.address || ''
      this.form.wish = last.wish || ''
      this.form.receiver = last.receiver || {
        name: '',
        phone: '',
        address: '',
        remark: ''
      }
      
      // ÊÅ¢Â§ç‰æõÂìÅÈÄâÊã©
      if (last.offerings) {
        last.offerings.forEach(selected => {
          const index = this.offerings.findIndex(item => item.name === selected.name)
          if (index !== -1) {
            this.offerings[index].qty = selected.qty
          }
        })
      }
    }
  }
}
</script>

